// @flow

import exec from 'weex-dingtalk-exec';
import { __nuva_require__, __nuva_define__, __nuva_define_remove__ } from './global-api/system-modules.js';
import EventEmitter from './global-api/EventEmitter.js';
import parseModules from './global-api/parseModules.js';

let { platform } = weex.config.env;
let globalEvent: Object = {};
if (platform !== 'Web'){
  globalEvent = weex.requireModule('globalEvent');
}

function rtFunc(method: string): Function {
  return function(cb: Function) {
    const config:{
      body: Object,
      onSuccess: Function,
      onFail: Function,
      context: ?Object
    } = {
      body: {
        plugin: 'runtime',
        action: method,
        args: {}
      },
      onSuccess: function(response){
        if (typeof cb === 'function'){
          cb(response);
        }
      },
      onFail: function(){

      },
      context: null
    };
    exec(config);
  };
}

function initDingtalkRequire(cb: Function){
    rtFunc('getModules')(cb);
}

let nuva: {
  getModules: ?Object,
  isReady: boolean,
  define: Function,
  require: Function,
  runtime: Object,
  init: Function,
  ready: Function,
  on: Function,
  off: Function,
  EventEmitter: Object
} = {
  getModules: null,
  isReady: false,
  define: __nuva_define__,
  require: function(id: string) : any{
    if (!id){
      return exec;
    } else {
      return __nuva_require__(id);
    }
  },
  runtime: {
    info: rtFunc('info'),
    _interceptBackButton: rtFunc('interceptBackButton'),
    _interceptNavTitle: rtFunc('interceptNavTitle'),
    _recoverNavTitle: rtFunc('recoverNavTitle'),
    _getModules: rtFunc('getModules')
  },
  init: function(){
    initDingtalkRequire(function(response){
      if(response){
        parseModules(response);
        nuva.isReady = true;
        nuva.getModules = response;
        EventEmitter.emit('__nuva_ready__');
      }
    });
  },
  ready: function(cb: Function){
    if (nuva.isReady){
      if (typeof cb === 'function'){
        cb();
      }
    } else {
      if (typeof cb === 'function'){
        EventEmitter.once('__nuva_ready__', function(){
          cb();
        });
      }
    }
  },
  on: function(type: string, handler: Function){
    globalEvent.addEventListener(type,function(e){
      const event:{
        preventDefault: Function,
        detail: Object
      } = {
        preventDefault: function () {
          console.warn('当前环境不支持 preventDefault')
        },
        detail: e
      };
      handler.call(this,event);
    });
  },
  off: globalEvent.removeEventListener,
  EventEmitter: EventEmitter
};

export default nuva;
